@media (max-width: 767px) {

    .parallax {
        height: 100%;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .App {
        text-align: center;
        margin: 0 auto;
        background-color: white;
        opacity: .60;
        height: 100vh;
        display: grid;
        grid-template-rows: repeat(3, 1fr) auto;
        grid-template-columns: 1fr;
    }

    .header {
        /* in App */
        grid-column: 1/-1;
        grid-row: 1/2;
        /* in header */
        display: grid;
        grid-template-columns: auto 125px;
        grid-template-rows: 100px;
        padding: 20px 0px 10px 0px;
        border-bottom: 10px solid #f1543f;
    }

    h1 {
        margin: 0px;
        padding: 10px 10px 10px 40px;
        grid-column: 1/2;
        grid-row: 1/-1;
    }

    .mapIcon {
        height: 75px;
        grid-column: 2/3;
        grid-row: 1/-1;
        padding-top: 10px;
    }

    .current {
        grid-column: 1/-1;
        grid-row: 2/3;
        padding: 10px;
        justify-self: center;
    }

    .daily {
        grid-column: 1/-1;
        grid-row: 3/4;
        margin-top: 10px;
    }

    .footer {
        grid-column: 1/-1;
        grid-row: 5/-1;
        padding: 10px;
    }

    .spinner {
        width: 50px;
    }

}


@media (min-width: 768px) {

    .parallax {
        height: 100%;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .App {
        text-align: center;
        margin: 0 auto;
        background-color: white;
        opacity: .60;
        height: 100vh;
        display: grid;
        grid-template-rows: 150px repeat(3, 1fr) auto;
        grid-template-columns: 1fr auto 1fr;
    }

    .header {
        /* in App */
        grid-column: 2/3;
        grid-row: 1/2;
        justify-self: center;
        /* in header */
        display: grid;
        grid-template-columns: auto 125px;
        grid-template-rows: 100px;
        padding: 20px 0px 0px 0px;
        border-bottom: 10px solid #f1543f;
    }

    .current {
        grid-column: 2/3;
        grid-row: 2/3;
        padding: 10px;
        justify-self: center;
    }

    .daily {
        grid-column: 2/3;
        grid-row: 3/4;
        margin-top: 10px;
    }

    .footer {
        grid-column: 1/-1;
        grid-row: 4/5;
        padding: 10px;
        justify-self: center;
    }

    h1 {
        margin: 0px;
        padding: 10px 10px 10px 40px;
        grid-column: 1/2;
        grid-row: 1/-1;
    }

    .mapIcon {
        height: 75px;
        padding-top: 10px;
    }

    .spinner {
        width: 50px;
    }

}





